import React, { Suspense } from "react";
import { Route, Routes, Navigate } from "react-router-dom";
import Layout from "./components/layout/Layout";
import Comment from "./components/comments/Comments";

import AllQuots from "./pages/AllQuots";
import { NotFound } from "./pages/NotFound";
import LoadingSpinner from "./components/UI/LoadingSpinner";

const AddQuots = React.lazy(() => import("./pages/AddQuots"));
const QuotDetail = React.lazy(() => import("./pages/QuotDetail"));

function App() {
  return (
    <Layout>
      <Suspense
        fallback={
          <div className="centered">
            <LoadingSpinner />
          </div>
        }
      >
        <Routes>
          <Route path="/" element={<Navigate replace to="/quotes" />} />
          <Route path="/quotes" element={<AllQuots />} />
          <Route path="/quotes/:quoteId/*" element={<QuotDetail />}>
            <Route path={"comment"} element={<Comment />} />
          </Route>
          <Route path="/new-quote" element={<AddQuots />} />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </Layout>
  );
}

export default App;
